import React from 'react';
import Button from './Button';


class Card extends React.Component {
  render() {
    return (
        <div className="card">
            
<div className="card-image">
  <img src={this.props.products.image}/>
</div>
<div className="card-info">
  <div className="card-name">{this.props.products.name}</div>
  <div className="card-price">{this.props.products.price} ₽</div>
  <Button 
  setter={this.props.setter} 
  id={this.props.products.id} 
  bought={this.props.products.bought}/>
</div>
        </div>
    );
}
}

export default Card;