import React from 'react';

class Button extends React.Component {
    render() {
    return (
        <div className="card-button">
            <button onClick={() => this.props.setter(this.props.id)}
            className="btn" disabled={this.props.bought}>
                Добавить в корзину
            </button>
        </div>
    );
}
}
export default Button;